import './styles.css';
import pixiAPI from './API/apiService.js';
import templateImageCard from './templates/templateImageCard.hbs';
import gsap from "gsap";

function parseData(data){
    console.log(data);
    const result = templateImageCard(data);
    section.innerHTML += result;
    btnLoad.removeAttribute('disabled');
    Array.from(document.querySelectorAll('img')).forEach
    ((el, index) => {
        gsap.to(el, {duration: 2, delay: index*0.3, opacity: 1, x: "+10"})
    });
}

let currentPage = 0;
const section = document.querySelector('#container');
const btnLoad = document.querySelector('#load-more-btn');
section.insertAdjacentHTML('beforebegin', `<form class="search-form" id="search-form">
<input
class="input"
  type="text"
  name="query"
  autocomplete="off"
  placeholder="Search images..."/>
  </form>`);
const input = document.querySelector('.input');

section.addEventListener('click', onClick)

function onClick(e){
    btnLoad.setAttribute('disabled', true);
    pixiAPI(input.value, ++currentPage, parseData);

}

pixiAPI('cat', ++currentPage, parseData);